
import { defineComponent } from "vue";

export default defineComponent({
  name: "FoodListItem",

  props: {
    name: String,
    cost: String,
    url: String,
  },

  methods: {
    onClicked(event: any) {
      console.log("eve", event, this.$el);
      this.$emit("onClicked", {
        name: this.name,
        cost: this.cost,
        url: this.url,
        el: this.$el,
      });
    },
  },

  mounted() {
    this.$el.addEventListener("transitionend", () => {
      console.log("end");
      this.$emit("transitionend");
    });
  },
});
